body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  width: 100%;
  height: 100%;
  padding-top: 10px;
  background-color:rgb(243, 147, 147);
  font-family: Lato, sans-serif;
  display: flex;
  flex-wrap: wrap;
 
  justify-content: center;
  align-items: center;
}
.freeCodeCampLogo{
  height: 40px;
}
.freeCodeCampLogo_contenedor{
  width: 100%;
  height: 50px;
  margin: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contenedorCalculadora{
  width: 400px;
  height: 600px;
  padding: 14px;
  background-color: rgb(240, 123, 240);
  border-radius: 20px;
  border: 5px solid white;
  margin-bottom: 2rem;
}
.fila{
  margin: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}











.botonContenedor{
    height: 83px;
    display: flex;
    flex: 1 1;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 38px;
    background-color: rgb(126, 31, 105);
    color: white;
    border-radius: 25px;
    border: 2px solid white;
    margin: 4px;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
    box-shadow: -1px 2.5px 1px 1px rgba(255, 255, 255, 0.712);
}
.botonContenedor:hover {
background-color: rgb(148, 110, 160);
box-shadow: none;
}
.operador{
    background-color: rgb(202, 50, 169);
    box-shadow: -1px 2.5px 1px 1px rgba(255, 255, 255, 0.712);
}
.operador:hover{
    background-color: rgba(250, 131, 224, 0.658);
    box-shadow:none;
    
}
.input{
    height: 75px;
    border-radius: 50px;
    margin-bottom: 20px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: bold;
    font-size: 30px;
    color: white;
    background-color: rgb(9, 31, 9);
    padding: 11px;
    padding-right: 30px;
    margin: 1px solid #888;
    box-shadow: -1px 4px 1px 1px white;
}
.clearBoton{
    height: 80px;
    font-size: 1.6em; 
    display: flex;
    flex: 1 1;
    background-color: rgb(170, 7, 170);
    margin-top: 8px;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: white;
    border: 2px solid white;
    cursor: pointer;
    border-radius: 20px;
    box-shadow: -1px 4px 1px 1px rgba(255, 255, 255, 0.788);
}
.clearBoton:hover{
    background-color: rgb(112, 27, 129);
    box-shadow:none;
}
 /*unidad relativa, va a ser 1.6 veces el tamaño de la letra de su contenedor */
